.hero {
    margin : 0;
    background-color: transparent;
    z-index: 99;
    h1,h3 {
        margin : 0;
        background-color: transparent;
        z-index: 99;
        color: white;
    }

    h1 {
        font-size : 4.5rem;
        //font-family: FontAwesome;
        font-weight: 100;
    }
    h3 {
        font-size: 1.1rem;
        padding-top : 2vh;
        font-weight: 200;
        filter: brightness(85%);
    }

    button {
        margin-top : 5vh;
        width : 13vw;
        height : 7.5vh;
        font-size : 1.1rem;
        background-color: #47c46d;
        color : white;
        border: none;
        cursor: pointer;
    }

    @media screen  and (orientation : portrait){
        button {
            width : 26vw;
            height : 9vh;
        }
    }
    
}

@font-face {
    font-family: FontAwesome;
    src: url('../../../fonts/fontawesome-webfont.woff');
}