.intr {
    width : 100vw;
    height : 205vh;
    background-color: white;
    margin-top: 13vh;

    h3 {
        letter-spacing: 1vw;
        text-align: center;
        font-size: 0.78rem;
        padding-top: 26vh;
        font-weight: 800;
    }

    h1 {
        text-align: center;
        max-width: 75vw;
        margin-left: 12.5vw;
        font-size: 2.3rem;
        margin-top: 5vh;
        font-weight: 100;
        color: #1f1e1e;
    }

    .images {
        display: flex;
        justify-content: center;
        margin-top: 10vh;
        .image1:hover {
            background-color:rgba($color: #000000, $alpha: 0.7);
            .header {
                opacity : 1;
                transform: scale(1);
            }
        }
        .image3:hover {
            background-color:rgba($color: #000000, $alpha: 0.7);
            .header {
                opacity : 1;
                transform: scale(1);
            }
        }
        .image1 {
            width : 45vw;
            height : 60vh;
            margin-right : 1vw;
            margin-left : 0vw;
            background-image: url('./project-image1.jpg');
            background-size: cover;
            transition: .6s;
            background-blend-mode: darken;
            cursor: pointer;

            .header {
                width : 22.5vw;
                padding-top: 20vh;
                font-weight: 300;
                font-size : 2rem;
                color: white;
                z-index: 99999;
                opacity: 0;
                transform: scale(0.75);
                transition: .6s;
            }
        }
       
        .image2 {
            @extend .image1;
            margin-right : 0vw;
            margin-left : 1vw;
            background-image: url('./project-image2.jpg');
            background-size: cover;
        }
        .image3 {
            width : 45vw;
            height : 60vh;
            margin-right : 1vw;
            margin-left : 0vw;
            background-image: url('./project-image3.jpg');
            background-size: cover;
            transition: .6s;
            background-blend-mode: darken;
            cursor: pointer;
            .header {
                width : 22.5vw;
                padding-top: 20vh;
                font-weight: 300;
                font-size : 2rem;
                color: white;
                z-index: 99999;
                opacity: 0;
                transform: scale(0.75);
                transition: .6s;
            }
        }
        .image4 {
            @extend .image3;
            margin-right : 0vw;
            margin-left : 1vw;
            background-image: url('./project-image4.jpg');
            background-size: cover;
        }
        @media screen and (orientation : portrait) {
            .image1,.image3 {
                width : 45vw;
                height : 30vh;
            }
        }
    }
}
.meet {
    width : 100vw;
    height : 100vh;
    background-color: white;
    display: flex;
    justify-content: center;
    padding-top: 20vh;
    padding-left: 2.5vw;

    .people1 {
        width : 40vh;
        height : 42vh;
        margin-right: 5vw;
        .img {
            width : 40vh;
            height: 40vh;
            background-image: url('./team-image1.jpg');
            background-size: cover;
        }
        h2 {
            text-align: center;
            margin: 0;
            padding: 0;
        }
        h3 {
            letter-spacing: normal;
        }
        .name {
            padding : 0;
        }

    }
    .people2 {
        @extend .people1;
        margin-right: 5vw;
        .img {
            background-image: url('./team-image2.jpg');
        }
    }
    .people3 {
        @extend .people1;
        .img {
            background-image: url('./team-image4.jpg');
        }
    }
    .people4 {
        @extend .people1;
        .img {
            background-image: url('./team-image3.jpg');
        }
    }
}

@media screen and (orientation : portrait) {
    .meet {
        display: grid;
        grid-template-columns: repeat(1,1fr);
        height : 250vh;

        .people1 {
            padding-bottom: 7.5vh;
            width : 60vw;
            margin-left : 20vw;
            margin-right : 0;
            h2 {
                margin-right : 0;
                text-align: none;
                padding-right: 5vw;
            }
            h3 {
                margin-right: 5vw;
            }
        }
    }

}
.header2 { 
    width : 100vw;
    background-color: white;
    padding-top: 10vh;
}