.nav {
    width : 100vw;
    position: fixed;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    color: white;
    padding: 3.5vh;
    transition: 0.65s;
    z-index: 999;
    h2 {
        float: left;
        padding-left: 7vh;
        font-weight: 400;
        font-size: 1.5rem;
    }
    div {
        display: grid;
        grid-template-columns: repeat(4,1fr);
        float: right;
        width : 40vw;
        margin-left: 5vw;

        h3 {
            font-weight: 400;
            font-size : 0.9rem;
            text-align: center;
            width: 6vw;
            padding-top: 1vh;
        }

        h3::after {
            width : 0%;
            height : 0.4vh;
            content: '';
            display : block;
            background-color: white;
            transition: .5s;
            will-change: width;
        }
        h3:hover::after {
            width : 100%;
        }
        h3:hover {
            cursor: pointer;
        }
    }

    @media screen and (orientation : portrait) {
        div {
            width : 70vw;
        }
    }
}
.nav2 {
    @extend .nav;
    background-color: #47c46d;
}