* {
    margin : 0;
    padding: 0;
}
.wrapper {
    padding-left : 7vw;
    padding-top : 40vh;
    h1,h3 {
        margin : none;
        background-color: transparent;
        z-index: 99;
    }
}
video {
    width : 100vw;
    position: fixed;
    filter: brightness(40%);
    z-index: -1;
}

@media screen and (orientation : portrait) {
    video {
        width : auto;
        height : 100%;
    }
}